/** Gallery */
.img-gallery{
  @extend .row;

  .img-blk {
    display: inline-block;
    @extend .col-md-3;
    @extend .col-xs-4;
    @extend .col-lg-2;
  }
}
.table td{
    overflow-wrap: break-word;
    word-wrap: break-word;
    -ms-word-break: break-all;
    word-break: break-all;
    word-break: break-word;
    -ms-hyphens: auto;
    -moz-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;
}



