/*Alerts*/
.alert-section {
    position: fixed;
    right: 10px;
    bottom: 0;
    width: 300px;
    z-index: 10000;
}

.alert-section .box {
    display: none;
    right: 10px; 
    bottom: 0;
}


/*END Alerts*/


