/* Sortable */
.js-sortable-handler{
    cursor: move;
}

/* END Sortable */

/* Slider blk */
.slider-blk{
	overflow-x:scroll;
	background-color:#fff;
}
/* END slider blk */

/* breadcrumb */
.breadcrumb.navigat{
    background: none!important;
    margin-bottom: 0;
}
.breadcrumb.navigat >li>a{
    color: #3c8dbc;
    text-decoration: underline;
}
.breadcrumb.navigat>li>a:focus, .navigat>li>a:hover {
    color: #72afd2;
    background: none;
    text-decoration: none;
}
.breadcrumb.navigat>li+li:before {
    content: ">";
    color: #3c8dbc;
}
/* end breadcrumb */
/* TEXT */
.list-unpadding{
    margin: 0;
    padding: 0 17px;
}
.box-lnk{
    color: #3c8dbc;
    text-decoration: underline;
}
/* END TEXT */

/*Form inline*/
.form-inline{
  margin: 10px 0;
  .form-group {
    margin-right: 10px;
    &>label{
      margin-right: 5px;
    }
  }
}
/*end form inline*/